                    <div class="content">
                        <div class="top_search">
                            <a-form-model layout="inline" :model="formInline">
                                <a-row>
                                    <a-col :span="21">
                                        <a-form-model-item>
                                            <a-button
                                                type="primary"
                                                html-type="submit"
                                            >
                                                搜索
                                            </a-button>
                                        </a-form-model-item>
                                        <a-form-model-item label="客户">
                                            <a-input
                                                v-model="formInline.user"
                                                placeholder="请输入客户"
                                            >
                                            </a-input>
                                        </a-form-model-item>
                                        <a-form-model-item label="年份">
                                            <a-input
                                                v-model="formInline.user"
                                                placeholder="请输入年份"
                                            >
                                            </a-input>
                                        </a-form-model-item>
                                        <a-form-model-item label="区间">
                                            <a-range-picker />
                                        </a-form-model-item>
                                        <a-form-model-item label="摘要关键字">
                                            <a-input
                                                v-model="formInline.user"
                                                placeholder="请输入摘要关键字"
                                            >
                                            </a-input>
                                        </a-form-model-item>
                                        <a-form-model-item>
                                            <a @click="show = !show">更多</a>
                                        </a-form-model-item>
                                    </a-col>
                                    <a-col :span="3" style="text-align: right">
                                        <a-form-model-item>
                                            <a>表头筛选</a>
                                        </a-form-model-item>
                                        <a-form-model-item>
                                            <a>操作指引</a>
                                        </a-form-model-item>
                                    </a-col>
                                </a-row>
                                <a-row v-if="show">
                                    <a-form-model-item label="条件a">
                                        <a-input v-model="formInline.user">
                                        </a-input>
                                    </a-form-model-item>
                                    <a-form-model-item label="条件b">
                                        <a-input
                                            v-model="formInline.user"
                                        ></a-input>
                                    </a-form-model-item>
                                    <a-form-model-item label="条件c">
                                        <a-input v-model="formInline.user">
                                        </a-input>
                                    </a-form-model-item>
                                    <a-form-model-item label="条件d">
                                        <a-input v-model="formInline.user">
                                        </a-input>
                                    </a-form-model-item>
                                    <a-form-model-item label="条件e">
                                        <a-input v-model="formInline.user">
                                        </a-input>
                                    </a-form-model-item>
                                </a-row>
                            </a-form-model>
                        </div>
                        <div
                            class="content_table"
                            :style="{
                                height: show
                                    ? 'calc(100% - 40px);'
                                    : 'calc(100% - 80px);',
                            }"
                        >
                            <a-button
                                type="primary"
                                :style="{
                                    left: show2 ? '15.7%' : '22px',
                                }"
                                class="shrink-button"
                                @click="show2 = !show2"
                            >
                                <a-icon type="caret-left" v-if="show2" />
                                <a-icon type="caret-right" v-if="!show2" />
                            </a-button>
                            <div class="left_list" v-if="show2">
                                <a-tree
                                    :tree-data="treeData"
                                    show-icon
                                    default-expand-all
                                    :default-selected-keys="['0-0-0']"
                                >
                                </a-tree>
                            </div>
                            <div
                                :style="{
                                    width: show2 ? '' : '100%',
                                }"
                                class="right_table"
                            >
                                <a-table
                                    :columns="columns"
                                    :data-source="data"
                                    :pagination="false"
                                    :scroll="{ x: 1300, y: 300 }"
                                    :rowClassName="rowClassName"
                                >
                                    <div
                                        slot="filterDropdown"
                                        slot-scope="{
                                            setSelectedKeys,
                                            selectedKeys,
                                            confirm,
                                            clearFilters,
                                            column,
                                        }"
                                        style="padding: 8px"
                                    >
                                        <a-input
                                            v-ant-ref="(c) => (searchInput = c)"
                                            placeholder="请输入"
                                            :value="selectedKeys[0]"
                                            style="
                                                width: 188px;
                                                margin-bottom: 8px;
                                                display: block;
                                            "
                                            @change="
                                                (e) =>
                                                    setSelectedKeys(
                                                        e.target.value
                                                            ? [e.target.value]
                                                            : []
                                                    )
                                            "
                                            @pressEnter="
                                                () =>
                                                    handleSearch(
                                                        selectedKeys,
                                                        confirm,
                                                        column.dataIndex
                                                    )
                                            "
                                        />
                                        <a-button
                                            type="primary"
                                            icon="search"
                                            size="small"
                                            style="
                                                width: 90px;
                                                margin-right: 8px;
                                            "
                                            @click="
                                                () =>
                                                    handleSearch(
                                                        selectedKeys,
                                                        confirm,
                                                        column.dataIndex
                                                    )
                                            "
                                        >
                                            搜索
                                        </a-button>
                                        <a-button
                                            size="small"
                                            style="width: 90px"
                                            @click="
                                                () => handleReset(clearFilters)
                                            "
                                        >
                                            取消
                                        </a-button>
                                    </div>
                                    <a-icon
                                        slot="filterIcon"
                                        slot-scope="filtered"
                                        type="search"
                                        :style="{
                                            color: filtered
                                                ? '#108ee9'
                                                : undefined,
                                        }"
                                    />
                                    <span slot="fx" slot-scope="fx">
                                        <a-tag
                                            v-if="fx === '借'"
                                            :color="'red'"
                                        >
                                            {{ fx }}
                                        </a-tag>
                                        <a-tag v-else :color="'lime'">
                                            {{ fx }}
                                        </a-tag>
                                    </span>
                                </a-table>
                                <a-pagination
                                    :total="50"
                                    :show-total="(total) => `共 ${total} 条`"
                                    show-size-changer
                                    show-quick-jumper
                                />
                            </div>
                        </div>
                    </div>
                </a-tab-pane>
                <a-tab-pane key="2" tab="明细账" force-render>
                    Content of Tab Pane 2
                </a-tab-pane>
                <a-tab-pane key="3" tab="凭证查询">
                    Content of Tab Pane 3
                </a-tab-pane>
            </a-tabs>
        </a-layout-content>
        <a-layout-footer>
            2014-2021 苏ICP备 15042426号-5 版权所有 企查 查科技有限公司
            增值电信业务许可证: 苏ICP证B2-20180251 企业aaaa
