import sessionUtils from '@data/session/sessionUtils.js';
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
const columns = [
    {
        fixed: 'left',
        width: 100,
        title: '序号',
        render: (text, record, index) => `${index + 1}`,
        sorter: (a, b) => a.age - b.age,
    },
    {
        fixed: 'left',
        title: '凭证日期',
        dataIndex: 'time',
        key: 'time',
        defaultSortOrder: 'descend',
        sorter: (a, b) => a.age - b.age,
        width: 200,
    },
    {
        title: '字',
        key: 'zi',
        width: 200,
        dataIndex: 'zi',
    },
    {
        title: '号',
        width: 200,
        key: 'hao',
        dataIndex: 'hao',
    },
    {
        width: 200,
        title: '摘要',
        key: 'zhaiyao',
        dataIndex: 'zhaiyao',
    },
    {
        width: 200,
        title: '对方科目',
        key: 'km',
        dataIndex: 'km',
        scopedSlots: {
            filterDropdown: 'filterDropdown',
            filterIcon: 'filterIcon',
        },
    },
    {
        title: '币种',
        width: 200,
        key: 'bz',
        dataIndex: 'bz',
        scopedSlots: {
            filterDropdown: 'filterDropdown',
            filterIcon: 'filterIcon',
        },
    },
    {
