    {
        title: '方向',
        width: 200,
        key: 'fx',
        dataIndex: 'fx',
        scopedSlots: { customRender: 'fx' },
    },
    {
        title: '金额',
        width: 200,
        key: 'je',
        dataIndex: 'je',
