        bz: '未知币种',
        wb: 22,
        jwb: 333,
        dwb: 31,
        bwb: 32,
        fx: '借',
        je: 999,
    },
];
export default {
    name: 'App',
    data() {
        this.formatter = formatter;
        this.XEUtils = XEUtils;
        return {
            userinfo: {},
            recommendList: [
                '上季节交易汇总',
                '年度交易考核',
                '昨日内部交易内容',
            ],
            systemList: ['客户中心', '财务中心', '项目中心', '考试中心'],
            formInline: {},
            show: false,
            show2: true,
            treeData: [
                {
                    title: '科目1',
                    key: 332,
                    children: [
                        { title: '科目1-1', key: '0-0-0-0' },
                        { title: '科目1-2', key: '0-0-0-1' },
                        { title: '科目1-3', key: '0-0-0-2' },
                    ],
                },
                { title: '科目2', key: 2 },
                { title: '科目3', key: 3 },
                { title: '科目4', key: 111 },
            ],
            data,
            columns,
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = current - 1;
                    this.queryParams.limit = pageSize;
                    this.queryParams.isChange = true;
                    this.pagination.pageSize = pageSize;
                    this.queryMerchantInitData();
                },
                showQuickJumper: false,
                total: 0,
                showTotal: () => `共${9999}条`,
                change: true,
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.queryParams.isChange = false;
                    this.pagination.current = current;
                    this.queryMerchantInitData();
                },
            },
        };
    },
    methods: {
        handleSearch(selectedKeys, confirm, dataIndex) {
            confirm();
            this.searchText = selectedKeys[0];
            this.searchedColumn = dataIndex;
        },
        handleReset(clearFilters) {
            clearFilters();
            this.searchText = '';
        },
        rowClassName(record, index) {
            let className = 'light-row';
            if (index % 2 === 1) className = 'dark-row';
            return className;
        },
        onSearch() {},
        goSystem(systemName) {
            console.log(systemName);
        },
    },
};
