<style lang="scss">
</style>
<style lang="scss" scoped>
.userNmae {
    text-align: right;
    line-height: 60px;
    font-size: 18px;
    padding-right: 20px;
}
.ant-layout-content {
    .ant-tabs {
        width: 100%;
        height: 100%;
        /deep/ .ant-tabs-card-content {
            height: 100%;
            .ant-tabs-tabpane {
                height: 100%;
            }
        }
    }
    .content {
        width: 100%;
        height: 100%;
        .content_table {
            position: relative;
            height: calc(100% - 96px);
            margin-top: 20px;
            width: 100%;
            .shrink-button {
                height: 30px;
                width: 22px;
                padding: 0 3px;
                position: absolute;
                z-index: 999;
                top: 50%;
                transform: translate(-100%, -50%);
            }
            .left_list {
                float: left;
                width: 15%;
                background: #fff;
                height: calc(100% - 20px);
                box-shadow: 0 0 5px 2px #dcdfe6;
            }
            .right_table {
                float: right;
                width: 84%;
                background: #fff;
                height: calc(100% - 20px);
                box-shadow: 0 0 5px 2px #dcdfe6;
                >>> .light-row {
                }
                >>> .dark-row {
                    background: #fafafa;
                }
            }
            .ant-pagination {
                margin-top: 20px;
                margin-right: 20px;
                text-align: right;
            }
        }
    }
}
.ant-layout-footer {
    width: 100%;
    -webkit-box-shadow: 0 0 5px 2px #dcdfe6;
    box-shadow: 0 0 5px 2px #dcdfe6;
    height: auto !important;
    min-height: 60px !important;
    z-index: 999;
    background: #fff !important;
    text-align: center;
    a {
        color: rgba(0, 0, 0, 0.65);
    }
}
</style>



